@import url('https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Splash&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #211826;
    transition: 0.4s ease-in-out;
  }
  
  html,
  body,
  #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  .scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
  }
  
  .container {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  header {
    position: absolute;
    z-index: 99;
    width: 100%;
    padding: 60px;
    background-color:#0E0713;
    .header-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .logo {
        font-weight: 900;
        letter-spacing: 2px;
        color: #fff;
      }
      nav {
        ul {
          margin: 0;
          padding: 0;
          display: flex;
          li {
            list-style: none;
            margin: 0 60px;
            &.btn {
              a {
                color: #fff;
                font-weight: 600;
                background: #23232a;
                padding: 16px 24px;
                border-radius: 10px;
              }

              
              
            }
            a {
              text-transform: capitalize;
              text-decoration: none;
              color: #fff;
            }
          }
        }
      }
    }
  }


  
  
  .title {
    font-size: 10rem;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    span {
      display: block;
    }
    @media (max-width: 1440px) {
      font-size: 6rem;
    }
  }
  
  .loading {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #171717;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .loading-bar-container {
    width: 100px;
    height: 32px;
    background: #272727;
  }
  
  .loading-bar {
    height: 32px;
    background: #f15946;
  }
  